.toolbar {
  justify-content: flex-end;
  padding: 16px 24px;
  display: flex;
  position: absolute;
  top: 180px;
  right: 0;
}

.theme-switch {
  align-items: center;
  display: flex;
}

.theme-switch__icon {
  color: currentColor;
  height: 15px;
  width: 15px;
  margin-left: 8px;
  margin-right: 8px;
}

.theme-switch__control {
  width: 32px;
  height: 16px;
  border-radius: 50em;
  flex-shrink: 0;
  align-items: center;
  padding: 3px 0;
  display: flex;
  position: relative;
}

.theme-switch__toggle {
  opacity: 0;
  height: 0;
  width: 0;
  pointer-events: none;
  margin: 0;
  padding: 0;
  position: absolute;
  top: 0;
  left: 0;
}

.theme-switch__track {
  width: 100%;
  height: 100%;
  color: rgba(0, 0, 0, 0);
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
  border-radius: inherit;
  z-index: 1;
  cursor: pointer;
  background-color: #333;
  position: absolute;
  top: 0;
  left: 0;
}

.theme-switch__marker {
  width: 13px;
  height: 13px;
  z-index: 2;
  pointer-events: none;
  background-color: #fff;
  border-radius: 50%;
  transition: transform .25s cubic-bezier(.4, 0, .2, 1);
  position: relative;
  transform: translateX(3px);
  box-shadow: 0 1px 1px rgba(0, 0, 0, .25);
}

.theme-switch__toggle:checked + .theme-switch__track {
  background-color: var(--accent);
}

.theme-switch__toggle:checked ~ .theme-switch__marker {
  transform: translateX(17px);
}

.theme-switch__toggle:focus + .theme-switch__track, .theme-switch__toggle:active + .theme-switch__track {
  box-shadow: 0 0 0 3px var(--accent);
}

.dark-theme {
