.backdrop {
  width: 100%;
  height: 100%;
  z-index: 2;
  transition: opacity var(--transition), visibility var(--transition);
  background-color: rgba(0, 0, 0, .17);
  position: fixed;
  top: 0;
  left: 0;
}

.modal {
  width: 280px;
