.header__library {
  display: block;
  margin: 0 auto;
  width: 100%;
  padding: 45px 20px;
  height: 230px;

  font-weight: 500;
  color: var(--header-color-text);

  background-color: #2a2a2a;
  background-image: linear-gradient(
      to right,
      rgba(0, 0, 0, 0.56),
      rgba(0, 0, 0, 0.56)
    ),
    url(../images/header-library-background-mobile@2x.jpg);
  background-position: top;
  background-repeat: no-repeat;
  background-size: cover;

  @include tablet {
    padding: 45px 60px 20px;
    background-image: linear-gradient(
        to right,
        rgba(0, 0, 0, 0.56),
        rgba(0, 0, 0, 0.56)
      ),
      url(../images/header-library-background-tablet@2x.jpg);
  }

  @include desktop {
    padding-top: 40px;
    background-image: linear-gradient(
        to right,
        rgba(0, 0, 0, 0.56),
        rgba(0, 0, 0, 0.56)
      ),
      url(../images/header-library-background-desktop@2x.jpg);
  }

  & .current::after {
    width: 69px;
  }
}

.storage {
  display: flex;
  justify-content: space-between;

  @include tablet {
    justify-content: center;
  }
}

.storage__item:first-child {
  @include tablet {
    margin-right: 15px;
  }
}

.storage__btn {
  display: block;

  padding-top: 14px;
  padding-bottom: 12px;

  min-width: 130px;

  @include tablet {
    min-width: 136px;
  }

  text-align: center;

  font-weight: 500;
  font-size: 12px;
  line-height: 1.33;
  text-transform: uppercase;

  color: var(--header-color-text);

  cursor: pointer;

  border: 1px solid var(--header-color-text);
  border-radius: 5px;

  // &:hover,
  // &:focus {
  //   background-color: $accentColor;
  //   border: 1px solid $accentColor;
  // }
}

.storage__input:checked ~ .storage__btn {
  background-color: var(--accent);
  border: 1px solid var(--accent);
  color: var(--header-color-text);
}

